import React, { useEffect, useState, useRef } from "react";
import { MapContainer, TileLayer, Marker, Popup, GeoJSON } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import { Card, Input} from "../components/ui"; // Lisää valitsemallesi Select-komponentti
//import hintaAlueet from "../data/postinumerot2025.geojson";
//import hintaAlueet from "../data/postinumerot2025webmercator.geojson"
//import rajat from "../data/kuntarajat.geojson"
import L from 'leaflet'; // Tuo Leaflet-kirjasto
import { useRadioGroup, Button, FormControl, FormControlLabel, FormLabel, Radio, RadioGroup, Select, InputLabel, MenuItem, Slider, TextField } from '@mui/material';


const LandCalculator = () => {
  const [area, setArea] = useState(0); // Pinta-ala (m²)
  const [selectedRegion, setSelectedRegion] = useState(null); // Valittu alue
  const [price, setPrice] = useState(null); // Laskettu hinta
  const [typeprice, setTypeprice] = useState(null);
  const [geojsonData, setGeojsonData] = useState(hintaalueet);
  const [geojsonKunnat, setGeojsonKunnat] = useState(null);
  const [selectedKunta, setSelectedKunta] = useState(null); // Valittu alue
  const mapRef = useRef(null); // mapRef sijoitettu ennen returnia
  const [priceType, setPriceType] = useState("residential"); // Oletushintatyyppi on asuinalue
  const [calculationBasis, setCalculationBasis] = useState("pinta-ala"); // Laskentaperuste: "pinta-ala" tai "rakennusoikeus"
  

  /*useEffect(() => {
    
    fetch("./data/postinumerot2025webmercator.geojson")
      .then((response) => response.json())
      .then((data) => setGeojsonData(data))
      .catch((error) => console.error("Virhe GeoJSON-tiedoston lataamisessa:", error));

    fetch("./data/kuntarajat.geojson")
      .then((response2) => response2.json())
      .then((data2) => setGeojsonKunnat(data2))
      .catch((error2) => console.error("Virhe kuntarajojen lataamisessa:", error2));
  }, []);
*/
  useEffect(() => {
    if (selectedRegion !== null) {
      setTypeprice(getPriceFromFeature(selectedRegion));
    }
  }, [priceType, selectedRegion]);

  const geoJsonStyle = (feature) => {
    const hinta = feature.properties.he_vakiy;
    let color;
    if (hinta > 4000) {
      color = "red";
    } else if (hinta > 2000) {
      color = "orange";
    } else {
      color = "green";
    }

    return {
      color: "black",
      fillColor: color,
      fillOpacity: 0.1,
      weight: 1,
    };
  };

  const geoJsonStyleKunnat = (feature) => {
    const hinta = feature.properties.landarea;
    let color;

    if (hinta > 4000) {
      color = "blue";
    } else if (hinta > 2000) {
      color = "yellow";
    } else {
      color = "red";
    }

    return {
      color: "red",
      fillColor: color,
      fillOpacity: 0.1,
      weight: 3,
    };
  };

  const onEachFeature = (feature, layer) => {
    layer.on({
      click: () => {
        setSelectedRegion(feature.properties);
      },
    });

    if (feature.properties && feature.properties.nimi) {
      layer.bindPopup(
        `<strong>Alue:</strong> ${feature.properties.nimi}<br />
         <strong>Hinta:</strong> ${feature.properties.he_vakiy} €/m²`
      );
    }
  };

  const onEachKunta = (feature, layer) => {
    if (feature.geometry.type === "Polygon" || feature.geometry.type === "MultiPolygon") {
      const bounds = layer.getBounds();
      const center = bounds.getCenter();

      const icon = L.divIcon({
        html: `<div class="map-label">${feature.properties.namefin}</div>`,
        className: 'map-label',
        iconSize: null,
        iconAnchor: [0, 0]
      });

      if (mapRef.current) {
        L.marker(center, { icon: icon }).addTo(mapRef.current);
      }
    }
  };

  const getPriceFromFeature = (feature) => {
    if (priceType === "residential") {
      return feature.he_naiset || 0;
    } else if (priceType === "industrial") {
      return feature.he_vakiy || 0;
    } else if (priceType === "commercial") {
      return feature.he_miehet || 0;
    }
    return 0;
  };

  const calculatePrice = () => {
    if (selectedRegion && area > 0) {
      const totalPrice = area * typeprice;
      setPrice(totalPrice);
    } else {
      alert("Valitse alue ja syötä kelvollinen pinta-ala.");
    }
  };

  return (
    <div style={{ padding: "20px", textAlign: "center" }}>
     
        <div style={{ width: "100%", height: "600px" }}>
          <h1 className="text-2xl font-bold mb-4">Maapohjan hinnan laskuri</h1>

          <MapContainer center={[61.5695, 21.9354]} zoom={10} style={{ width: "100%", height: "100%" }} ref={mapRef}>
            <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
            {geojsonKunnat && (
              <GeoJSON
                data={geojsonKunnat}
                style={geoJsonStyleKunnat}
                onEachFeature={onEachKunta}
                interactive={false}
              />
            )}
            {geojsonData && (
              <GeoJSON
                data={geojsonData}
                style={geoJsonStyle}
                onEachFeature={onEachFeature}
              />
            )}
          </MapContainer>
            <br />
          <Card className="w-full max-w-lg p-4 mb-4">
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Hintatyyppi</InputLabel>
            <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={priceType}
                label="Age"
                onChange={(e) => setPriceType(e.target.value)}
            >
                <MenuItem value="residential">Asuin</MenuItem>
                <MenuItem value="industrial">Teollisuus</MenuItem>
                <MenuItem value="commercial">Liiketontti</MenuItem>
            </Select>
            </FormControl>
                <br />
            <div className="mb-4">
              <div className="flex space-x-4">
              <FormControl>
                <FormLabel id="demo-radio-buttons-group-label">Laskentaperuste</FormLabel>
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    defaultValue="female"
                    name="radio-buttons-group"
                >
                    <FormControlLabel value="pinta-ala" control={<Radio />} label="Pinta-ala" checked={calculationBasis === "pinta-ala"} onChange={() => setCalculationBasis("pinta-ala")}/>
                    <FormControlLabel value="rakennusoikeus" control={<Radio />} label="Rakennusoikeus" checked={calculationBasis === "rakennusoikeus"} onChange={() => setCalculationBasis("rakennusoikeus")}/>
                </RadioGroup>
            </FormControl>
              </div>
            </div>
            <div className="mb-4">
              <label className="block text-sm font-medium mb-2">{calculationBasis} (m²):<br /> 
                <TextField
                id="outlined-number"
                label="m²"
                type="number"
                slotProps={{
                    inputLabel: {
                    shrink: true,
                    },
                }}
                value={area}
                onChange={(e) => setArea(Number(e.target.value))}
                /></label>
              <Slider
                size="small"
                defaultValue={50}
                min={0}
                max={10000}
                aria-label="Small"
                valueLabelDisplay="auto"
                onChange={(e) => setArea(Number(e.target.value))}
                value={area}
                step="1"
                />
                              
            </div>

            <div className="mb-4">
              <label className="block text-sm font-medium mb-2">Valittu alue: </label>
              {selectedRegion ? selectedRegion.nimi : "Ei aluetta valittuna. Klikkaa karttaa valitaksesi alueen."}
            </div>
            {price !== null && (
              <div className="mt-4 text-center">
                <p className="text-lg font-semibold"><h2><b>Laskettu hinta: {price.toLocaleString()} €</b></h2></p>
                <p className="text-xl font-bold"></p>
              </div>
            )}
            <Button variant="contained" color="success" onClick={calculatePrice}>
            Laske hinta
            </Button>
            
          </Card>
        </div>
    </div>
  );
};

export default LandCalculator;

